body {
    margin: 0;
    padding: 0;
}

.playground {
    margin-top: 0px;
    margin-left: 0px;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    position: relative;
    background: url(../images/city_background.png);
    background-repeat: repeat-x;
    background-size: cover;
    overflow: hidden;
    background-position: 100px 0;

    animation: backgroundMove 40s 0s infinite linear;
}

.car2 {
    background: url(../images/cars2.png) no-repeat -731px -357px;
	width: 76px;
	height: 25px;
    zoom: 3;
    position: absolute;
    left: 20px;
    bottom: 32px;
    z-index: 1;
}

.car1 {
    width: 80px;
    height: 28px;
    background: url(../images/cars2.png) no-repeat -744px -13px;
    zoom: 3;
    position: absolute;
    left: 20px;
    bottom: 7px;
    z-index: 2;
}

.road {
    height: 150px;
    width: 100%;
    background: url(../images/road.png);
    background-size: contain; 
    position: absolute;
    bottom: 0;
    left: 0;
    background-repeat: repeat-x;
    background-position: 100px 0;

    animation: roadMove 2s 0s infinite linear;
}

@keyframes backgroundMove {
    100% {
        background-position: -1800px 0;
    }
}

@keyframes roadMove {
    100% {
        background-position: -1800px 0;
    }
}

